.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);

  width: 528px;
  min-height: 581px;
  padding: 40px;

  background-color: var(--primary-white);
  border-radius: 4px;

  transition: transform var(--transition-duration)
  var(--transition-timing-function);

  .backdrop--is-hidden & {
    transform: translate(-50%, -50%) scale(0.8);
  }
}

.modal__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  color: var(--tittle-color);

  margin-bottom: 12px;
}

.modal__fields-wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.modal__checkbox-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
}

.modal__checkbox {
  appearance: none;

  width: 16px;
  height: 15px;

  border: 2px solid var(--tittle-color);
  border-radius: 2px;

  background-image: url("data:image/svg+xml,%3Csvg width='16' height='15' viewBox='0 0 16 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.95703 7.75166L3.88825 7.68604L3.81923 7.75141L2.93123 8.59258L2.85486 8.66492L2.93097 8.73753L6.42671 12.0724L6.49574 12.1382L6.56476 12.0724L14.069 4.91352L14.1449 4.84116L14.069 4.76881L13.1873 3.92764L13.1183 3.86183L13.0493 3.92761L6.49577 10.1735L3.95703 7.75166Z' fill='white' stroke='white' stroke-width='0.2'/%3E%3C/svg%3E%0A");
  background-position: center;
  background-size: 0%;
  background-repeat: no-repeat;
  background-origin: border-box;

  transition: border-color var(--transition-duration)
  var(--transition-timing-function), background-color var(--transition-duration)
  var(--transition-timing-function), background-size var(--transition-duration) var(--transition-timing-function);

  &:checked {
    border-color: var(--accent-color);
    background-color: var(--accent-color);
    background-size: 100%;
  }
}

.modal__checkbox-label {
  line-height: 1.71;
  user-select: none;
}

.modal__link {
  color: var(--accent-color);
  text-underline-offset: 4px;
}

.modal__btn {
  display: block;
  margin: 0 auto;
}
